document.getElementById('search-bar').addEventListener('input', function () {
    const searchValue = this.value.toLowerCase();
    if (searchValue.length > 0) {
        searchArticles(searchValue);
    } else {
        clearSuggestions();
    }
});

function searchArticles(query) {
    let url = "https://api.nytimes.com/svc/news/v3/content/nyt/all.json?api-key=CgkxxT7s9PckcmY15CVedbNMJIe0zAP1";
    fetch(url)
        .then(response => response.json())
        .then(data => {
            const filteredArticles = data.results.filter(article =>
                article.title.toLowerCase().includes(query)
            );
            displaySuggestions(filteredArticles);
        });
}

function displaySuggestions(articles) {
    const suggestionsContainer = document.getElementById('suggestions');
    suggestionsContainer.innerHTML = '';
    articles.forEach(article => {
        const suggestion = document.createElement('div');
        suggestion.className = 'suggestion-item';
        suggestion.textContent = article.title;
        suggestion.addEventListener('click', () => {
            window.open(article.url, '_blank');
        });
        suggestionsContainer.appendChild(suggestion);
    });
}

function clearSuggestions() {
    const suggestionsContainer = document.getElementById('suggestions');
    suggestionsContainer.innerHTML = '';
}

// Other functions remain the same...
function displayMostRecentArticle(article) {
    const mostRecentDiv = document.getElementById("most-recent-article");
    mostRecentDiv.innerHTML = ''; 

    const articleDiv = document.createElement("div");
    articleDiv.className = 'article main-article';

    const imgSrc = (article.multimedia && article.multimedia.length > 2) ? article.multimedia[2].url : 'placeholder-image.jpg';
    const articleUrl = article.url || '#';
    const author = article.byline || "Unknown Author";
    const category = article.section || "General";

    articleDiv.innerHTML = `
        <p class="category">${category}</p>
        <h2>${article.title}</h2>
        <p><strong>Author:</strong> ${author}</p>
        <img src="${imgSrc}" alt="${article.title}">
        <p>${article.abstract}</p>
        <a href="${articleUrl}" target="_blank">Read more</a>
    `;

    mostRecentDiv.appendChild(articleDiv);
}

function displayArticles(articles) {
    const otherArticles = document.getElementById("other-articles");
    const moreArticles = document.getElementById("more-articles");

    otherArticles.innerHTML = ''; 
    moreArticles.innerHTML = ''; 

    articles.slice(1, 6).forEach(article => {
        addArticle(article, otherArticles);
    });

    articles.slice(6).forEach(article => {
        const link = document.createElement("a");
        link.href = article.url;
        link.textContent = article.title;
        link.target = "_blank";
        moreArticles.appendChild(link);
        moreArticles.appendChild(document.createElement("br"));
    });
}
